<style scoped lang="less">
  .toggles {
    display: inline-block;
    padding: 0 45px;
    height: 39px;
    line-height: 39px;
    margin-top: 5px;
    cursor: default;
    background: #aadaff;
    color: #F9FAFC;
    font-size: 18px;
    &.toggle-act {
      background: #1D8CE0;
    }
  }

  .content-main > div {
    height: 90%;
    overflow: auto;
  }

  .add-ordinary-manage {
    display: flex;
    flex-direction: column;
    align-items: center;
    li {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 400px;
      padding: 15px 0;
    }
  }
</style>
