        <ul class="add-ordinary-manage">
          <li><el-input placeholder="姓名"></el-input></li>
          <li><el-input placeholder="性别"></el-input></li>
          <li><el-input placeholder="年龄"></el-input></li>
          <li><el-input placeholder="亚专科分类"></el-input></li>
          <li><el-input placeholder="联系电话"></el-input></li>
          <li><el-input placeholder="职称"></el-input></li>
          <li>
            <span>照片</span>
            <el-upload class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/" multiple><i class="el-icon-upload"></i>
              <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
              <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </li>
          <li>
