      }
    },
    props: ['a'],
    components: {

    },
    mounted(){

    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      toggleCW(i){
        this.toggleCWshow = i;
      },
      aas(i,arr){

      }
    }
  }
</script>



